<template>
  <div class="layout" :class="{'layout-hide-text': spanLeft === spanLeftFold}">
    <grid-row>
      <grid-col :span="spanLeft"  :style="style">
        <div v-on:mouseover="expandMenu" v-on:mouseleave="foldMenu">
          <d-menu :active-name="activeName" :theme="theme" width="auto">
            <div class="layout-logo-left"></div>
            <d-menu-item name="retry" icon="zap" color="#98D358" :size="iconSize" :to="`/${ curLang }/tool/retry`">
              <span class="layout-text">{{ langConfig.retry }}</span>
            </d-menu-item>
            <d-menu-item name="archive" icon="package" color="#7FB3D5" :size="iconSize" :to="`/${ curLang }/tool/archive`">
              <span class="layout-text">{{ langConfig.archive }}</span>
            </d-menu-item>
            <d-menu-item name="alarmRule" icon="bell" color="#CA6F1E" :size="iconSize" :to="`/${ curLang }/tool/alarmRule`">
              <span class="layout-text">{{ langConfig.alarmRule }}</span>
            </d-menu-item>
            <d-menu-item name="alarmRuleDefault" icon="target" color="#98D358" :size="iconSize"  :to="`/${ curLang }/tool/alarmRuleDefault`">
              <span class="layout-text">{{ langConfig.alarmRuleDefault }}</span>
            </d-menu-item>
            <d-menu-item name="metric"  icon="edit" color="#7FB3D5" :size="iconSize"   :to="`/${ curLang }/tool/metric`">
              <span class="layout-text">{{ langConfig.metric }}</span>
            </d-menu-item>
            <d-menu-item name="taskHistory" :to="`/${ curLang }/tool/taskHistory`" v-if="$store.getters.isAdmin">
              <icon name="command" color="#9D9D9D" :size="iconSize"/>
              <span class="layout-text">{{ langConfig.taskHistory }}</span>
            </d-menu-item>
            <d-menu-item name="operateHistory" icon="target" color="#ffaad5" :size="iconSize" :to="`/${ curLang }/tool/operateHistory`" v-if="$store.getters.isAdmin">
              <span class="layout-text">{{ langConfig.operateHistory }}</span>
            </d-menu-item>
          </d-menu>
        </div>
      </grid-col>
      <grid-col :span="spanRight">
        <div class="layout-header">
          <d-button type="borderless" icon="menu" class="toggle-btn" @click="toggleClick"></d-button>
          <div class="layout-breadcrumb">
            <d-breadcrumb>
              <d-breadcrumb-item href="#">{{langHeaderConfig.tool}}</d-breadcrumb-item>
              <d-breadcrumb-item>{{submenu}}</d-breadcrumb-item>
            </d-breadcrumb>
          </div>
        </div>
        <div class="layout-content">
          <router-view></router-view>
        </div>
      </grid-col>
    </grid-row>
  </div>
</template>
<script>
// import compoLang from '../../i18n/components.json'
import submenu from '../../mixins/submenu.js'

export default {
  components: {},
  mixins: [ submenu ],
  data () {
    return {
      menu: 'tool'
    }
  },
  computed: {
    curLang () {
      return this.$i18n.locale
    }
  }
}

</script>

<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
  }
  .layout-logo-left{
    width: 90%;
    height: 0px;
    line-height: 20px;
    color: #ffffff;
    background: #ffffff;
    border-radius: 3px;
    margin: 15px auto;
    text-align: center;
  }
  .toggle-btn{
    margin-left: 15px;
  }
  .layout-header {
    padding-top: 10px;
    padding-left: 20px;
    box-sizing: border-box;
    flex-shrink: 0;
    /*background: #ffffff;*/
  }
  .layout-breadcrumb{
    padding: 10px 15px 0;
    display: inline-block;
  }
  .layout-content{
    padding-top: 20px;
    padding-bottom: 15px;
    overflow: hidden;
    /*background: #ffffff;*/
    border-radius: 4px;
    height: calc(100% - 50px);
  }
  .layout-hide-text .layout-text {
    display: none;
  }
  .dui-menu-dark.dui-menu-vertical .dui-menu-item-active:not(.dui-menu-submenu) {
    background: rgb(84, 92, 100);
  }
</style>



